.wrapper_banner{
      width: 100%;
      height: 380px;
      background: var(--light);
      position: relative;
}

.banner_content{
      width: 100%;
      height: 310px;
      position: relative;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      position: absolute;
      background: var(--gradient-45);
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.banner_content__img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, var(--tertiary));
      toP: 0;
      left: 0;
      z-index: 1;
      opacity: .8;
}

.banner_content__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      filter: grayscale(1);
      mix-blend-mode: multiply;
      opacity: .8;
}

.container.banner_img{
      position: relative;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      z-index: 2;
}

.banner_title{
      width: fit-content;
      height: auto;
      display: flex;
      flex-direction: column;
}

.banner_title h1{
      text-transform: uppercase;
      font-size: 4rem;
}

.banner_title p{
      font-size: 1.6rem;
      text-align: center;
}

.banner_social{
      position: absolute;
      bottom: 9rem;
      right: 4rem;
      display: flex ;
      align-items: center;
      gap: 1rem;
      z-index: 10;
}

.wrapper_online{
      position: absolute;
      top: 4rem;
      right: 4rem;
      display: flex;
      gap: .5rem;
      z-index: 3;
}

.online_avatar,
.online_avatar img{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      display: flex;
}

.online_avatar{
      position: relative;
}

.online_dots{
      position: absolute;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: var(--light);
      top: 0;
      right: 0;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-100);
}

.wrapper_online span{
      text-transform: uppercase;
      padding: .4rem 1.5rem;
      background: var(--theme);
      height: fit-content;
      border-radius: 999px;
}

.content_user{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10;
}

.container.user{
      position: relative;
      display: flex;
      height: 100%;
      align-items: flex-end;
      padding-bottom: 3.5rem;
      gap: 3rem;
}

.wrapper_user{
      width: 242px;
      height: 308px;
      padding: 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      background: var(--light);
      border-radius: .4rem;
      box-shadow: 2rem 2rem 2.5rem rgba(0,0,0,.2);
}

.user_avatar{
      width: 136px;
      height: 136px;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--theme);
      padding: .5rem;
}

.user_avatar img{
      width: 126px;
      height: 126px;
      border-radius: 50%;
}

.wrapper_user h2{
      color: var(--theme);
      font-size: 2rem;
      margin-top: 1rem;
}

.wrapper_user strong{
      color: var(--tertiary);
      font-size: 1.4rem;
}

.wrapper_user p{
      color: var(--grys-300);
      font-size: 1.4rem;
}

.user_menu{
      height: 38px;
      display: flex;
      align-items: center;
      margin-top: 1rem;
      gap: 1rem;
      justify-content: center;
}

.user_menu a{
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.05);
      border-radius: 50%;
}

.user_menu a *{
      font-size: 1.6rem;
      color: var(--theme);
}

.wrapper_relation h2{
      position: relative;
      text-transform: uppercase;
      color: var(--light);
      font-size: 1.2rem;
      text-align: left;
      line-height: 5rem;
      padding-left: 6rem;
}

.wrapper_relation h2:before{
      width: 8rem;
      height: .2rem;
      background: var(--theme);
      left: 0;
      bottom: 1rem;
}

.relation_box{
      display: flex;
      height: 70px;
      gap: 1rem;
      align-items: center;
}

.relation_box__avatar,
.relation_box__avatar img{
      width: 70px;
      height: 70px;
      border-radius: .4rem;
      display: flex;
}

.relation_box__avatar{
      box-shadow: 1rem 1rem 1.5rem rgba(0,0,0,.2);
}

.relation_box__data{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      height: 7rem;
}

.relation_box__data h3{
      text-transform: uppercase;
      font-size: 1.4rem;
      positioN: relative;
}

.relation_box__data h3:before{
      content: attr(data-id);
      background: var(--theme);
      padding: .4rem 1.5rem;
      border-radius: 999px;
      font-size: 1rem;
      right: -8rem;
      top: -2.5rem;
}

.relation_box__data p{
      color: var(--grys-300);
      margin-top: 2rem;
      font-size: 1.2rem;
}

.relation_box__data a{
      padding: .4rem 1.5rem;
      border-radius: .4rem;
      background: var(--secondary);
      color: var(--light);
      text-transform: uppercase;
}