.page_content{
      width: 100%;
      height: auto;
      padding: 6rem 0;
}

.container.page{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.page_navigator{
      width: 58rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
}

.btn_navigator{
      width: calc(100% / 3);
      height: 4.2rem;
      text-transform: uppercase;
      color: var(--light);
      background: var(--secondary);
}

.btn_navigator:first-child{
      background: var(--tertiary);
      border-radius: .4rem 0 0 .4rem;
}

.btn_navigator:last-child{
      background: var(--theme);
      border-radius: 0 .4rem .4rem 0;
}

.btn_navigator.active{
      background: #222;
}

.content_gifs{
      margin-top: 5rem;
      width: 100%;
}

.wrapper_page{
      width: 100%;
      height: auto;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
}