.empty_content{
      width: 100%;
      min-height: 30rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
      margin-top: 5rem;
}

.empty_icon *{
      font-size: 10rem;
      color: var(--grys-300);
}

.empty_content h2{
      font-size: 2rem;
      color: var(--grys-300);
      text-align: center;
}